* {
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #777;
  background-color: rgba(224, 221, 221, 0.096);
  letter-spacing: 0.25px;
}

.nav-bar {
  z-index: 1;
}

.account_circle {
  margin-right: 50px;
}

.footer-list ul {
  display: flex;
  justify-content: center;
}

.footer-list ul li a i {
  font-size: 1.5em;
  padding: 20px;
}

.rights {
  font-size: 12px;
}

.showcase {
  background-image: url(/myProfile/static/media/bg1.a1f31fae.jpg);
  background-color: #cccccc;
  height: 70vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.showcase-content {
  background-color: rgba(186, 221, 250, 0.301);
  text-align: center;
  font-weight: 200px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  padding: 5px 10px;
}

.head-01 {
  display: block;
  letter-spacing: 20%;
  font-size: 45px;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
}

.head-02 {
  display: block;
  letter-spacing: 20.2px;
  padding: 0px 2px;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
}

