.summary-list {
  list-style-type: disc;
}

.skills-tab {
  list-style-type: square;
}

.section-heading {
  margin-bottom: 10px;
  padding: 15px;
}

.images-cert {
  display: flex;
  width: 90%;
  margin-bottom: 10px;
}

.company-name {
  font-size: 16px;
  font-weight: 100px;
}

.job-title {
  font-size: 16px;
}

ul .list-item li {
  list-style-type: disc;
}

.card-heading {
  text-align: center;
  padding: 10px 0px;
}

a.logo {
  padding-left: 3.5%;
  letter-spacing: 0.12rem;
}

#nav-mobile {
  margin-top: 64px;
}

.side-menu {
  z-index: 1;
}

